body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-header {
  background: white;
  border-bottom: 1px solid #e5e5e5;
  padding: 1rem;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #1a1a1a;
  margin: 0;
}

.header-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.search-container {
  position: relative;
  width: 300px;
}

.search-input {
  width: 100%;
  padding: 0.5rem 2.5rem 0.5rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  outline: none;
}

.search-input:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.search-results {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  max-height: 240px;
  overflow-y: auto;
  z-index: 10;
  margin-top: 0.25rem;
}

.search-result-item {
  padding: 0.75rem 1rem;
  border-bottom: 1px solid #f3f4f6;
  cursor: pointer;
}

.search-result-item:hover {
  background-color: #f9fafb;
}

.search-result-item:last-child {
  border-bottom: none;
}

.search-result-name {
  font-weight: 500;
  font-size: 0.875rem;
  margin-bottom: 0.25rem;
}

.search-result-title {
  font-size: 0.75rem;
  color: #6b7280;
}

.upload-new-btn {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  background-color: #f3f4f6;
  color: #374151;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.upload-new-btn:hover {
  background-color: #e5e7eb;
}

.employee-info {
  max-width: 1200px;
  margin: 1rem auto 0;
  padding: 1rem;
  background-color: #eff6ff;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.employee-details h2 {
  font-size: 1.125rem;
  font-weight: 600;
  color: #1e3a8a;
  margin: 0 0 0.25rem 0;
}

.employee-details p {
  color: #1d4ed8;
  margin: 0.25rem 0;
}

.close-btn {
  background: none;
  border: none;
  color: #2563eb;
  cursor: pointer;
  font-size: 1rem;
  padding: 0.25rem;
}

.close-btn:hover {
  color: #1d4ed8;
}

.main-content {
  flex: 1;
  overflow: hidden;
}

.upload-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: #f9fafb;
}

.file-upload-area {
  border: 2px dashed #d1d5db;
  border-radius: 0.5rem;
  padding: 2rem;
  text-align: center;
  cursor: pointer;
  transition: border-color 0.2s;
  max-width: 400px;
  width: 100%;
}

.file-upload-area:hover {
  border-color: #9ca3af;
}

.upload-icon {
  width: 3rem;
  height: 3rem;
  color: #9ca3af;
  margin: 0 auto 1rem;
}

.upload-title {
  font-size: 1.125rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}

.upload-subtitle {
  font-size: 0.875rem;
  color: #6b7280;
  margin-bottom: 1rem;
}

.upload-note {
  font-size: 0.75rem;
  color: #9ca3af;
}

.error-message {
  margin-top: 1rem;
  padding: 0.75rem;
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  border-radius: 0.5rem;
}

.loading-spinner {
  width: 2rem;
  height: 2rem;
  border: 2px solid #3b82f6;
  border-top: 2px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 1rem;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.employee-node {
  border: 2px solid;
  border-radius: 0.5rem;
  padding: 0.75rem;
  cursor: pointer;
  transition: box-shadow 0.2s;
  min-width: 0;
  max-width: 100%;
}

.employee-node:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.employee-node.selected {
  box-shadow: 0 0 0 2px #3b82f6;
}

.employee-node.level-0 { background-color: #f3e8ff; border-color: #c4b5fd; }
.employee-node.level-1 { background-color: #dbeafe; border-color: #93c5fd; }
.employee-node.level-2 { background-color: #dcfce7; border-color: #86efac; }
.employee-node.level-3 { background-color: #fef3c7; border-color: #fcd34d; }
.employee-node.level-4 { background-color: #fed7aa; border-color: #fdba74; }
.employee-node.level-5 { background-color: #fce7f3; border-color: #f9a8d4; }
.employee-node.level-6 { background-color: #e0e7ff; border-color: #a5b4fc; }
.employee-node.level-7 { background-color: #f3f4f6; border-color: #9ca3af; }

.employee-name {
  font-weight: 600;
  font-size: 0.875rem;
  margin-bottom: 0.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.employee-title {
  font-size: 0.75rem;
  color: #4b5563;
  line-height: 1.2;
}

.employee-reports {
  font-size: 0.75rem;
  color: #6b7280;
  margin-top: 0.25rem;
}
